<template>
  <div class="monthReport">
    <header-buttons
      :showRefresh="true"
      @buttonClickEvent="buttonClickEvent"
      cssType="1"
    ></header-buttons>
    <div class="searchDiv">
      <div>
        <span>月份:</span>
        <el-date-picker
          v-model="month"
          type="month"
          value-format="yyyy-MM"
          size="mini"
          placeholder="选择月"
        ></el-date-picker>
      </div>
      <!-- <div>
        <span>班次:</span>
        <el-select v-model="shift" placeholder="请选择" size="mini">
          <el-option
            v-for="(item, index) in shiftOptions"
            :key="index"
            :label="item.shiftName"
            :value="index"
          ></el-option>
        </el-select>
      </div> -->
      <Div>
        <el-button size="mini" type="primary" @click="search">查询</el-button>
        <el-button size="mini" @click="handleExport">导出</el-button>
      </Div>
    </div>
    <VXETable
      id="table"
      :height="$options.filters.getTableHeight(200)"
      :tableName="''"
      :tableLoading="tableLoadings"
      :tableDatas="tableDatas"
      :tableColumns="tableColumns"
      :tablePage="tablePages"
      @sortChange="sortChange"
      @onTablePageChange="handleMainPageChange"
      ref="xGrid"
    ></VXETable>
  </div>
</template>
<script>
// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE } from '@/utils/constantsFile';
import { getMonthReport, listAllShiftInfoOfStringValue } from '@/api/fbs.js';
export default {
  name: 'monthReport',
  components: {
    VXETable
  },
  data() {
    return {
      month: new Date(),
      shift: '',
      shiftOptions: [],
      tableLoadings: false,
      tableDatas: [],
      tableColumns: [
        {
          field: 'dateTime',
          title: '日期',
          width: 170,
          visible: true,
          align: 'center',
          fixed: 'left',
          sortable: true
        },
        {
          field: 'shiftName',
          title: '班次',
          width: 120,
          visible: true,
          align: 'center',
          fixed: 'left'
        },
        // {
        //   field: 'runValue',
        //   title: '运行值',
        //   width: 120,
        //   visible: true,
        //   align: 'center'
        // },
        {
          field: 'totalCoolingCapacityAfter',
          title: '总供冷量',
          width: 120,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'bromineMachineCoolingCapacity',
          title: '溴机供冷量',
          visible: true,
          width: 120,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'purchaseElectricity',
          title: '采购电量(kW·h)',
          visible: true,
          width: 150,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'gasTurbineGeneration',
          title: '燃气发电量(kW·h)',
          width: 150,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'internetPower',
          title: '上网电量(kW·h)',
          width: 150,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'photovoltaicPowerGeneration',
          title: '光伏发电量(kW·h)',
          width: 150,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'totalElectricityConsumption',
          title: '总用电量(kW·h)',
          width: 150,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'twoABCharge',
          title: '2A/2B电量(kW·h)',
          width: 150,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'electricRefrigerationConsumption',
          title: '电制冷主机用电量(kW·h)',
          width: 200,
          visible: true,
          align: 'center',
          formatter({ cellValue, row, column }) {
            return parseFloat(cellValue.toFixed(2));
          }
        },
        {
          field: 'electricRefrigerationRuntime',
          title: '电制冷运行时间(h)',
          width: 180,
          visible: true,
          align: 'center'
        },
        {
          field: 'electricRefrigerationRadio',
          title: '电制冷COP',
          width: 120,
          visible: true,
          align: 'center'
        }
      ],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      sortType: 'DESC'
    };
  },
  created() {
    // this.getShift();
    this.findList();
  },
  methods: {
    buttonClickEvent(type) {
      if (type === 'refresh') {
        this.month = new Date();
        this.tablePages.currentPage = 1;
        this.tablePages.pageSize = 10;
        this.findList();
      }
    },
    search() {
      this.findList();
    },
    handleExport() {
      this.findList('export');
    },
    sortChange(property, order) {
      this.sortType = order.toLocaleUpperCase();
      // this.orderByData = {
      //   orderColumn: '',
      //   orderType: ''
      // };
      // this.orderByData = {
      //   orderColumn: property,
      //   orderType: order
      // };
      this.handleMainPageChange(1, this.tablePages.pageSize);
    },
    handleMainPageChange(currentPage, pageSize) {
      this.tablePages.currentPage = currentPage;
      this.tablePages.pageSize = pageSize;
      this.findList();
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList(paramExport) {
      var that = this;
      this.tableLoadings = true;
      try {
        let shiftStartTime = '';
        if (typeof this.month === 'string') {
          shiftStartTime = this.month + '-01 00:00:00';
        } else {
          shiftStartTime =
            this.moment(this.month).format('YYYY-MM') + '-01 00:00:00';
        }
        // let startTime = `${this.moment(new Date().setMonth(new Date().getMonth() - 3)).format('YYYY-MM-DD')} 00:00:00`;
        // let endTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`;

        // if (this.dateTime[0] !== undefined) {
        //   startTime = this.dateTime[0];
        //   endTime = this.dateTime[1];
        // }

        var params = {
          pageNum: this.tablePages.currentPage,
          pageSize: this.tablePages.pageSize,
          startTime: shiftStartTime,
          sortType: this.sortType
        };

        console.log(params);

        if (paramExport === 'export') {
          params.pageNum = 1;
          params.pageSize = 9999999;
        }

        const res = await getMonthReport({}, params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(res);
          this.tableDatas = res.rows;
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
          if (paramExport === 'export') {
            // 过滤需要展示的列
            var showArr = [];
            for (var i = 0; i < this.tableColumns.length; i++) {
              if (
                this.tableColumns[i].type !== 'radio' &&
                this.tableColumns[i].type !== 'checkbox'
              ) {
                showArr.push(this.tableColumns[i].field);
              }
            }
            that.$refs.xGrid.exportData('月报表', res.rows, showArr);
          }
        } else {
          this.tableDatas = [];
          this.tablePages.total = 0;
          // 提示异常信息
          showWarningMessage(res.message);
          this.tableLoadings = false;
        }
      } catch (error) {
        this.tableLoadings = false;
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoadings = false;
      }
    },
    getShift() {
      listAllShiftInfoOfStringValue({}, {}).then(res => {
        this.shiftOptions = res.rows;
        console.log(res);
      });
    }
  }
};
</script>
